form {
    display: grid;
    grid-template-columns: repeat(2, 400px);
    grid-column: 1;

    .form-group {
        padding-bottom: 1vw;
        grid-row: 1/1;
        input, textarea, button {
            padding: 20px;
            width: 500px;
        }
        input[type="date"] {
            width: 114%;
        }
        button {
            width: 543px;
            text-transform: uppercase;
            font-weight: 600;
            text-align: left;
            width: 100px;
            border-radius: 50px;
            cursor: pointer;
        }
    }
    :nth-child(1) {
        grid-row: 1/1;
    }
    :nth-child(2) {
        grid-row: 2/2;
    }
    :nth-child(3) {
        grid-row: 3/3;
    }
    :nth-child(4) {
        grid-row: 4/4;
    }

}